import { BrowserModule } from '@angular/platform-browser';
import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LocationStrategy, PathLocationStrategy } from '@angular/common';
import { HttpModule } from '@angular/http';
import { ReactiveFormsModule } from '@angular/forms';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { ApplyFormPostService } from './Services/apply-form-post.service';
import { NavBarColorService } from './Services/nav-bar-color.service';
import { SharedModule } from './shared.module';
import { AppComponent } from './app.component';
import { CareerComponent } from './pages/career/career.component';
import { HomeComponent } from './pages/home/home.component';
import { AddService } from './Services/add.service';
import { MobileApplicationsComponent } from './pages/mobile-applications/mobile-applications.component';
import { UiUxComponent } from './pages/ui-ux/ui-ux.component';
import { AboutComponent } from './pages/about/about.component';
import { MouseWheelDirective } from './Directives/mouse-scroll.directive';
import { EnquiryMailSendService } from './Services/enquiry-mail-send.service';
import { ContactModalService } from './Services/contact-modal.service';

import {
  HammerGestureConfig,
  HAMMER_GESTURE_CONFIG,
} from '@angular/platform-browser';
import { WebApplicationsComponent } from './Pages/web-applications/web-applications.component';
import { ERPComponent } from './Pages/ERP/ERP.component';
import { EcommerceComponent } from './Pages/E-commerce/E-commerce.component';
import { AIAnalyticsComponent } from './Pages/AI-Analytics/AI-Analytics.component';
import { IOTComponent } from './Pages/IOT/IOT.component';
import { ContactUsComponent } from './Pages/contact-us/contact-us.component';
import { OwlModule } from 'ng2-owl-carousel';
import { AnimateOnScrollModule } from 'ng2-animate-on-scroll';

declare var Hammer: any;

export class MyHammerConfig extends HammerGestureConfig  {
  buildHammer(element: HTMLElement) {
    let mc = new Hammer(element, {
      touchAction: "pan-x"
    });
    return mc;
  }
}

const appRoutes: Routes = [
  { path: '', component: HomeComponent },
  { path: 'career', component: CareerComponent },
  { path: 'about', component: AboutComponent },
  { path: 'home', component: HomeComponent },
  { path: 'mobile-development', component: MobileApplicationsComponent},
  { path: 'ui-ux', component: UiUxComponent },
  { path: 'contact-us', component: ContactUsComponent },
  { path: 'web-applications', component: WebApplicationsComponent},
  { path: 'ERP', component: ERPComponent},
  { path: 'E-commerce', component: EcommerceComponent},
  { path: 'ai-analytics', component: AIAnalyticsComponent},
  { path: 'IOT', component: IOTComponent}
];

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    CareerComponent,
    MobileApplicationsComponent,
    UiUxComponent,
    AboutComponent,
    MouseWheelDirective,
    WebApplicationsComponent,
    ERPComponent,
    ContactUsComponent,
    EcommerceComponent,
    AIAnalyticsComponent,
    IOTComponent
  ],
  imports: [
    CommonModule,
    BrowserModule,
    BrowserAnimationsModule,
    HttpModule,
    ReactiveFormsModule,
    SharedModule,
    OwlModule,
    AnimateOnScrollModule.forRoot(),
    RouterModule.forRoot(
      appRoutes,
      {enableTracing: false,
      useHash: true} // <-- debugging purposes only
    )
  ],
  providers: [AddService, ApplyFormPostService, EnquiryMailSendService, NavBarColorService, ContactModalService,{ 
    provide: HAMMER_GESTURE_CONFIG, 
    useClass: MyHammerConfig 
    }],
  bootstrap: [AppComponent]
})
export class AppModule {

}



