import { Component, OnInit } from '@angular/core';
import {services} from '../../pages/home/home';
import { NavBarColorService } from '../../Services/nav-bar-color.service'

@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.css']
})
export class FooterComponent implements OnInit {

  footerList: Object;

  backgroundColor: string = 'transparent';
  name: string = 'close';
  navMenu: boolean = false;
  currentPage: string = ' ';
  logo: string = "./assets/images/logo_white.png";
  hamBurgerColor: string = 'white';
  tFlag: boolean = true;
  shadowStatus: string = ' ';
  displayMenu: boolean = false;
  servicesList: Object;

  state: string = 'hide';

  constructor() {
    this.footerList = services;
   }

  ngOnInit() {
  }

  animateMe() {
    this.state = (this.state === 'hide' ? 'show' : 'hide');
  }

  iconAnimate() {
    if (this.name == 'close') {
      this.name = 'open';
      this.navMenu = true;
      this.backgroundColor = "black";
      this.logo = './assets/images/logo_white.png';
      this.hamBurgerColor = 'white';
      document.querySelector("body").style.overflow = "hidden";
    }
    else {
      this.name = 'close';
      this.navMenu = false;
      document.querySelector("body").style.overflow = "auto";
      if (this.tFlag == true) {
        this.backgroundColor = "transparent";
        this.logo = './assets/images/logo_white.png';
        this.hamBurgerColor = 'white';
      }
      else if (this.tFlag == false && this.currentPage == "home") {
        this.backgroundColor = "#19191E";
        this.logo = './assets/images/logo_white.png';
        this.hamBurgerColor = 'white';
      }
      else {
        this.backgroundColor = "white";
        this.logo = './assets/images/logo_black.png';
        this.hamBurgerColor = 'black';
      }
    }
  }

  navBackground(argument) {
    document.querySelector("body").style.overflow = "auto";
    if (argument == 'home') {
      this.backgroundColor = "transparent";
      this.name = 'close';
      this.navMenu = false;
      this.currentPage = "home";
      this.logo = './assets/images/logo_white.png';
    }
    else {
      this.backgroundColor = "white";
      this.shadowStatus = "navbar-shadow";
      this.name = 'close';
      this.navMenu = false;
      this.currentPage = "default";
      this.logo = './assets/images/logo_black.png';
      this.hamBurgerColor = 'black';
    }
  }

}



