import { NgModule } from "@angular/core";
import {CommonModule} from '@angular/common';
import { MenuListComponent } from "./Components/menu-list/menuList.component";
import { HeaderComponent } from "./components/header/header.component";
import { FooterComponent } from "./components/footer/footer.component";
import { MenuSectionComponent } from "./components/menu-section/menu-section.component";
import { ScrollifyComponent } from "./Components/scrollify/scrollify.component";
import { RouterModule } from '@angular/router';
import { BrowserModule } from "@angular/platform-browser";
import { FormsModule } from '@angular/forms'; //added in shared module inorder to use in multiple places
@NgModule({
    declarations: [MenuListComponent, HeaderComponent, FooterComponent, MenuSectionComponent, ScrollifyComponent],
    imports: [RouterModule, BrowserModule, FormsModule ],
    exports: [MenuListComponent, HeaderComponent, FooterComponent, MenuSectionComponent, ScrollifyComponent, FormsModule]
})
export class SharedModule {} 


