import { Injectable } from '@angular/core';
import { Component, OnInit, OnChanges, AfterViewInit, HostListener, Inject } from '@angular/core';
import { EnquiryMailSendService, IMessage } from './enquiry-mail-send.service';
import { ViewChild, ElementRef } from '@angular/core';
import { NgForm, FormGroup, FormControl } from '@angular/forms';
import { NG_VALIDATORS, Validator, Validators, AbstractControl, ValidatorFn } from '@angular/forms';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';

@Injectable()
export class ContactModalService implements OnChanges, OnInit{
  captchaCalc: any;
  userValue: string;
  x: any;
  y: any;
  accessApply: boolean = false;	
  myForm: NgForm;
  @ViewChild('closeMailerModal') closeMailerModal: ElementRef; //closing after submitting
  // @ViewChild('myForm') myForm: NgForm; //for reseting form
  formValidity: boolean = false;
  model = new User('', '', '', '');
  constructor(private EnquiryMailSendService: EnquiryMailSendService) { }
  sendEmail(message: User) {
    console.log(message);
    this.EnquiryMailSendService.sendEmail(message).subscribe(res => {
      console.log('AppComponent Success', res);
      // this.closeMailerModal.nativeElement.click();
      // this.myForm.reset();
    }, error => {
      console.log('AppComponent Error', error);
      // this.closeMailerModal.nativeElement.click();
    })
  }
  ngOnInit() {

  }

  /* Calculating captcha value */
  captcha() {
    this.x = Math.floor((Math.random() * 9) + 1);
    document.getElementById("captchaaX").innerHTML = this.x;
    this.y = Math.floor((Math.random() * 9) + 1);
    document.getElementById("captchaaY").innerHTML = this.y;
    this.captchaCalc = this.x + this.y;
  }

  /* Comparing captcha value to user entered value */
  captchaCalcFunc(value: string) {
    console.log("Entered value:", value);
    console.log("Calculated value:", this.captchaCalc);
    this.userValue = value;
    if (value == this.captchaCalc) {
      this.accessApply = true;
    }
    else {
      this.accessApply = false;
    }
  }

  onKey(event: any) {
    console.log("checker Called")
    if (this.myForm.valid) {
      this.formValidity = true;
    } else {
      this.formValidity = false;
    }
  }

  formValid(){
    if (this.userValue == this.captchaCalc && this.formValidity) {
      return false;
    }
    else {
      return true;
    }
  }
  ngOnChanges() {

  }

}

export class User {
  constructor(
    public name: string,
    public email: string,
    public message: string,
    public captcha: string
  ) { }
} 



