import { Component, OnInit, HostListener } from '@angular/core';
import { NavBarColorService } from '../../Services/nav-bar-color.service';
import { FormBuilder,NgForm,FormGroup,FormControl, NgModel } from '@angular/forms';
import { NG_VALIDATORS, Validator, Validators, AbstractControl, ValidatorFn} from '@angular/forms';
import { EnquiryMailSendService, IMessage } from '../../Services/enquiry-mail-send.service';
import { ViewChild, ElementRef} from '@angular/core';

declare var jquery: any;
declare var $: any;
declare var grecaptcha: any;

@Component({
  selector: 'app-contact-us',
  templateUrl: './contact-us.component.html',
  styleUrls: ['./contact-us.component.css']
})
export class ContactUsComponent implements OnInit {
  deviceHeight: number;
  rForm:FormGroup;
  recaptcha:boolean = false;
 
  @ViewChild('captchaRef2') captchaRef2: ElementRef;
  private _reCaptchaId: number;


  fullName:string="";
  cname:string="";
  email:string="";
  mobile:string="";
  msg:string="";
  name: any;
  forminfo: any;

  constructor(private fb: FormBuilder,private navBarColorService: NavBarColorService,private EnquiryMailSendService: EnquiryMailSendService) { 
    
    this.rForm = fb.group({
      'fullName' : [null, Validators.compose([Validators.required, Validators.minLength(3)])],
      'cname' : [null, Validators.compose([Validators.required, Validators.minLength(3)])],
      'mobile' : [null, Validators.compose([Validators.required, Validators.minLength(10)])],
      'email' : [null, Validators.compose([Validators.required])],
      'msg' : [null, Validators.compose([Validators.required])]
    });

  
    this.deviceHeight = window.innerHeight;
    this.navBarColorService.navBarBackgroundColor.next("transparent");
    this.navBarColorService.logoColor.next("./assets/images/logo_white.png");
    this.navBarColorService.hamBurger.next("white");
    this.navBarColorService.currentPageName.next("about-us");
  }
  @ViewChild('closeMailerModal') closeMailerModal: ElementRef; //closing after submitting
	// @ViewChild('myForm') myForm: NgForm; //for reseting form


  sendEmail(message: IMessage) {
     //console.log(message);
     message = {};
    message.name = this.fullName;
    message.email = this.email;
    message.mobile = this.mobile;
    message.message = this.msg;
		this.EnquiryMailSendService.sendEmail(message).subscribe(res => {
      console.log('AppComponent Success', res);

      var x = document.getElementById("snackbar")
      x.className = "show";
      setTimeout(function(){ x.className = x.className.replace("show", ""); }, 3000);

			// this.myForm.reset();
		}, error => {
			console.log('AppComponent Error', error);
		})
  }
  

  /* Changing navbar color on respective scrolling positions */
  @HostListener('window:scroll', ['$event'])
  doSomething(event) {
    if (window.pageYOffset > 675) {
      this.navBarColorService.navBarBackgroundColor.next("white");
      this.navBarColorService.logoColor.next("./assets/images/logo_black.png");
      this.navBarColorService.hamBurger.next("black");
      this.navBarColorService.currentPageName.next("career");
      this.navBarColorService.tFlagValue.next(false);
      console.log("done! navbar color changed")
    }
    else {
      this.navBarColorService.navBarBackgroundColor.next("transparent");
      this.navBarColorService.logoColor.next("./assets/images/logo_white.png");
      this.navBarColorService.hamBurger.next("white");
      this.navBarColorService.currentPageName.next("career");
      this.navBarColorService.tFlagValue.next(true);
    }
   
  }

 

  ngOnInit() {
     window.scrollTo(0, 0);
  }

  resolvedCaptcha(){
    this.recaptcha = true;
  }

  ngAfterViewInit(){
    const grecaptcha = (window as any).grecaptcha;
    if (grecaptcha) {
      this._reCaptchaId = grecaptcha.render(this.captchaRef2.nativeElement, {
      'sitekey': '6LdSNVQUAAAAAIst-6WxkY4zh4DWUO_0anpnZBXd',
      'callback': (response) => this.resolvedCaptcha(),
      'expired-callback': () => grecaptcha.reset(this._reCaptchaId)
    });
    }

    console.log("inside"+window.pageYOffset);

    }

}



