import { Component, OnInit, HostListener } from '@angular/core';
import { policiesList, jobListing } from './careers';
import { FormBuilder, FormGroup, FormControl, Validators, NgForm, NgModel } from '@angular/forms';
import { ApplyFormPostService } from '../../Services/apply-form-post.service';
import { NavBarColorService } from '../../Services/nav-bar-color.service';
import { EnquiryMailSendService, IMessage } from '../../Services/enquiry-mail-send.service';

declare var jquery: any;
declare var $: any;
declare var $getVal: Object;
@Component({
  selector: 'app-career',
  templateUrl: './career.component.html',
  styleUrls: ['./career.component.css']
})
export class CareerComponent implements OnInit {
  policies:Object;
  testResponse:Object;
  jobList:any;
  jobShow: number = 0;
  id: any;
  description: any;
  requirements: any;
  title: any;
  experience: any;
  education: any;
  x:any;
  y:any;
  captchaCalc:any;
  rForm:FormGroup;
  accessApply:boolean = false;
  userValue:string;
  getVal: Object;
   /*------- Form validation variables ---------*/
   fullName:string="";
   email:string="";
   mobile:string="";
   name: any;
   forminfo: any;
   post:string="";

  constructor(private _getService: ApplyFormPostService,private fb: FormBuilder, private applyFormPostService: ApplyFormPostService, private navBarColorService:NavBarColorService,private EnquiryMailSendService: EnquiryMailSendService) { 
    this.policies = policiesList;
    // this.jobList = jobListing;

    /*--------- Form building -----------*/
    this.rForm = fb.group({
      'fullName' : [null, Validators.compose([Validators.required, Validators.minLength(3)])],
      'mobile' : [null, Validators.compose([Validators.required, Validators.minLength(10)])],
      'email' : [null, Validators.compose([Validators.required])],
      'captcha' : [null, Validators.compose([Validators.required])]
    });

    this.navBarColorService.navBarBackgroundColor.next("transparent");
    this.navBarColorService.logoColor.next("./assets/images/logo_white.png");
    this.navBarColorService.hamBurger.next("white");
    this.navBarColorService.currentPageName.next("career");
  }

  

  sendEmail(message: IMessage) {
    message = {};
    message.name = this.fullName;
    message.email = this.email;
    message.mobile = this.mobile;
    message.fileName = this.fileName;
    message.filePath = this.filePath;
    message.fileType = this.fileType;
    message.posting = this.post;
   this.EnquiryMailSendService.sendEmail(message).subscribe(res => {
     console.log('AppComponent Success', res);
     this.formReset();
   }, error => {
     console.log('AppComponent Error', error);
     this.formReset();
   })
 }

  /* Changing navbar color on respective scrolling positions */
  @HostListener('window:scroll', ['$event']) 
  doSomething(event) {
    if(window.pageYOffset > 675 ){
      this.navBarColorService.navBarBackgroundColor.next("white");
      this.navBarColorService.logoColor.next("./assets/images/logo_black.png");
      this.navBarColorService.hamBurger.next("black");
      this.navBarColorService.currentPageName.next("career");
      this.navBarColorService.tFlagValue.next(false);
      console.log("done! navbar color changed")
    }
    else {
      this.navBarColorService.navBarBackgroundColor.next("transparent");
      this.navBarColorService.logoColor.next("./assets/images/logo_white.png");
      this.navBarColorService.hamBurger.next("white");
      this.navBarColorService.currentPageName.next("career");
      this.navBarColorService.tFlagValue.next(true);
    }
  }

  ngOnInit() {
    window.scrollTo(0, 0);
    // this.getVal = this.applyFormPostService.insertData();
    // console.log(this.getVal);

    this.applyFormPostService.insertData().subscribe(
      data => {
        this.testResponse = data;
        console.log("I CANT SEE DATA HERE in ts: ", this.testResponse);

        for(var i=0;i<Object.keys(this.testResponse).length;i++){
          this.testResponse[i].requirements = (this.testResponse[i].requirements).split("*");
        }
        
        this.jobList = this.testResponse;
      }
    );

  }

  ngAfterViewInit(): void {

  }

 
  filePath: string = "";
  fileName: string = "";
  fileType: string = "";
  fileChange(event){
      alert($('input[type=file]').val());
      this.filePath = URL.createObjectURL(event.target.files[0]);
      this.fileType = event.target.files[0].type;
      this.fileName = event.target.files[0].name.substr(0, event.target.files[0].name.lastIndexOf('.'));
  }

  /* Dynamically setting job details on clicked job modal */
  jobDetail(jobId){
    if(this.jobShow ==jobId)
        this.jobShow=0
    else
        this.jobShow= jobId;
  }

  jobDescription(jobId){
    console.log(jobId);
    let job = this.jobList.find(o => o.job_id === jobId);
    console.log(job);
     this.description = job.description;
     this.id = job.job_id;
     this.requirements = job.requirements;
     this.title = job.job_title;
     this.post = job.job_title;    //Mail purpose
     this.education = job.education;
     this.experience = job.experience;  
  }

  /* Calculating captcha value */
  captcha() {
     this.x = Math.floor((Math.random() * 9) + 1);
    document.getElementById("captchaX").innerHTML = this.x;
     this.y = Math.floor((Math.random() * 9) + 1);
    document.getElementById("captchaY").innerHTML = this.y;
     this.captchaCalc = this.x + this.y;
  }

  /* Comparing captcha value to user entered value */
  captchaCalcFunc(value: string) {
    console.log("Entered value:", value);
    console.log("Calculated value:", this.captchaCalc);
    this.userValue = value;
    if( value == this.captchaCalc){
      this.accessApply = true;
    }
    else{
      this.accessApply = false;
    }
  }

  formReset(){
    (<HTMLFormElement>document.getElementById("applyForm")).reset();
  }

  
  formValid() {
    if( this.userValue == this.captchaCalc && this.rForm.valid){
      return false;
    }
    else{
      return true;
    }
  }

  
  /* sending the form data to applyform service */
  // myInsert() {
  //   this.applyFormPostService.insertData(this.fullName,this.email,this.mobile);
  //   alert('Data Inserted Successfully');
  //   (<HTMLFormElement>document.getElementById("applyForm")).reset();
  //   console.log(this.applyFormPostService.postResponse);
  // }




}


