import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Subject } from 'rxjs/Subject';
import {BehaviorSubject } from 'rxjs/BehaviorSubject';

@Injectable()
export class NavBarColorService {

  navBarBackgroundColor: BehaviorSubject<any> = new BehaviorSubject("transparent");
  logoColor: BehaviorSubject<any> = new BehaviorSubject("./assets/images/logo_white.png");
  hamBurger: BehaviorSubject<any> = new BehaviorSubject("white");
  currentPageName: BehaviorSubject<any> = new BehaviorSubject("home");
  tFlagValue: BehaviorSubject<boolean> = new BehaviorSubject(false);
  
  constructor() { }

}



