import { Component, OnInit } from "@angular/core";
import { menuList } from "../../Services/menu-list";
import { HttpClientModule } from '@angular/common/http';

// do the following declarations in order for jQuery to work
declare var jquery: any;
declare var $: any;

@Component({
    selector:"app-menu-list",
    templateUrl: './menuList.component.html',
    styleUrls: ['./menuList.component.css']
})
export class MenuListComponent implements OnInit {
    menuList: Object;
    menuColumn: number
    menuListPosition: string;
    className: string
    constructor(){}
    ngOnInit(){
        this.menuColumn = 3;
        this.menuList = menuList;
        this.menuListPosition = 'menu-list-section'
    }
    ngAfterViewInit(): void {
        $(document).ready(function () {
            $("a").mouseenter(function () {
                $(this).find("path").attr("fill", "#ccc");
            });
            $("a").mouseleave(function () {
                $(this).find("path").attr("fill", "#fff");
            });
        });
    }
}


