import { Component, OnInit, Directive, ElementRef, ChangeDetectorRef } from '@angular/core';
import { trigger, state, style, transition, animate, keyframes } from '@angular/animations';
import { menuList } from '../../Services/menu-list';
import { NavBarColorService } from '../../Services/nav-bar-color.service'
import { Subscription } from 'rxjs/Subscription';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css'],
  animations: [
    trigger('myAwesomeAnimation', [
      transition(':enter', [
        style({ transform: ' translateY(-120%)' }),
        animate('500ms cubic-bezier(0.075, 0.82, 0.165, 1)', style({ transform: ' translateY(0)' }))
      ]),
      transition(':leave', [
        style({ transform: ' translateY(0)' }),
        animate('900ms cubic-bezier(0.075, 0.82, 0.165, 1)', style({ transform: ' translateY(-120%)' }))
      ])
    ])
  ]
})



export class HeaderComponent implements OnInit {

  backgroundColor: string = 'transparent';
  name: string = 'close';
  navMenu: boolean = false;
  currentPage: string = ' ';
  logo: string = "./assets/images/logo_white.png";
  hamBurgerColor: string = 'white';
  tFlag: boolean = true;
  shadowStatus: string = ' ';
  displayMenu: boolean = false;
  servicesList: Object;

  state: string = 'hide';

  constructor(private navBarColorService: NavBarColorService, private ref:ChangeDetectorRef) {
    this.servicesList = menuList;
    this.navBarColorService.navBarBackgroundColor.subscribe((navBarBackgroundColor: any) => {
      this.backgroundColor = navBarBackgroundColor;
      if(this.backgroundColor == 'white'){
        this.shadowStatus = "navbar-shadow";
      }else{
        this.shadowStatus = " ";
      }
    });
    this.navBarColorService.logoColor.subscribe((logoColor: any) => {
      this.logo = logoColor;
    });
    this.navBarColorService.hamBurger.subscribe((hamBurger: any) => {
      this.hamBurgerColor = hamBurger;
    });
    this.navBarColorService.currentPageName.subscribe((currentPageName: any) => {
      this.currentPage = currentPageName;
    });
    this.navBarColorService.tFlagValue.subscribe((tFlagValue: any) => {
      this.tFlag = tFlagValue;
    });
  }

  ngOnInit() {
    
  }

  animateMe() {
    this.state = (this.state === 'hide' ? 'show' : 'hide');
  }

  iconAnimate() {
    if (this.name == 'close') {
      this.name = 'open';
      this.navMenu = true;
      this.backgroundColor = "black";
      this.logo = './assets/images/logo_white.png';
      this.hamBurgerColor = 'white';
      document.querySelector("body").style.overflow = "hidden";
    }
    else {
      this.name = 'close';
      this.navMenu = false;
      document.querySelector("body").style.overflow = "auto";
      if (this.tFlag == true) {
        this.backgroundColor = "transparent";
        this.logo = './assets/images/logo_white.png';
        this.hamBurgerColor = 'white';
      }
      else if(this.tFlag == false && this.currentPage == "home"){
        this.backgroundColor = "#19191E";
        this.logo = './assets/images/logo_white.png';
        this.hamBurgerColor = 'white';
      }
      else{
        this.backgroundColor = "white";
        this.logo = './assets/images/logo_black.png';
        this.hamBurgerColor = 'black';
      }
    }
  }

  navBackground(argument) {
    document.querySelector("body").style.overflow = "auto";
    if (argument == 'home') {
      this.backgroundColor = "transparent";
      this.name = 'close';
      this.navMenu = false;
      this.currentPage = "home";
      this.logo = './assets/images/logo_white.png';
    }
    else {
      this.backgroundColor = "white";
      this.shadowStatus = "navbar-shadow";
      this.name = 'close';
      this.navMenu = false;
      this.currentPage = "default";
      this.logo = './assets/images/logo_black.png';
      this.hamBurgerColor = 'black';
    }
  }

}





